/*----- GLOABL -----*/

.tl-dark-light-view-box {
    position: fixed;
    left: 25px;
    top: 50%;
    transition: all 0.3s;
    transform: translateY(-50%);
    width: 36px;
    height: 70px;
    border-radius: 18px;
    z-index: 999;
    background-color: var(--tl-btn-bg);
    cursor: pointer;
}

.tl-dark-light-view-box.has-clicked {
    background: var(--black);
}

.tl-dark-light-view-box .tl-dark-light-view-toogle-dot {
    position: absolute;
    width: 36px;
    height: 36px;
    transition: all 0.3s;
    background: #fff;
    border-radius: 50%;
    left: 0;
    top: 0;
}

.tl-dark-light-view-box .tl-dark-light-view-toogle-dot.pos-bottom {
    top: 34px;
}

.tl-dark-light-view-box::before {
    position: absolute;
    left: 50%;
    top: 5px;
    font-family: "Font awesome 5 pro";
    content: "\f185";
    color: #fff;
    transform: translateX(-50%);
}

.tl-dark-light-view-box::after {
    position: absolute;
    left: 50%;
    bottom: 5px;
    font-family: "Font awesome 5 pro";
    content: "\f186";
    color: #fff;
    transform: translateX(-50%);
}

html {
    scroll-behavior: smooth;
    overflow-x: hidden;
}

body {
    font-family: "Roboto", sans-serif !important;
    overflow-x: hidden;
}

h1,
h2 {
    font-family: var(--roboto);
    color: var(--black);
}

h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
    color: inherit;
}

p {
    font-size: 16px;
    line-height: 26px;
}

a {
    transition: 0.4s ease;
    text-decoration: none !important;
    color: var(--black);
    vertical-align: top;
    display: inline-block;
    cursor: pointer !important;
}

a:hover {
    text-decoration: none;
    color: inherit;
}

span {
    display: inline-block;
}

i {
    display: inline-block;
}

input,
textarea,
button {
    background: transparent;
    border: 0;
    padding: 0;
    transition: 0.4s ease;
}

input:focus,
textarea:focus,
button:focus {
    outline: none;
}

img {
    max-width: 100%;
}

ul {
    padding-left: 0 !important;
    margin-bottom: 0 !important;
    list-style: none;
}

input::-moz-placeholder {
    -moz-transition: 0.4s;
    transition: 0.4s;
    opacity: 1;
    color: #53545b;
}

input::placeholder {
    transition: 0.4s;
    opacity: 1;
    color: #53545b;
}

::-moz-selection {
    /* Code for Firefox */
    color: #fff;
    background: #23cc88;
}

::selection {
    color: #fff;
    background: #23cc88;
}

textarea::-moz-placeholder {
    -moz-transition: 0.4s;
    transition: 0.4s;
    opacity: 1;
    color: #53545b;
}

textarea::placeholder {
    transition: 0.4s;
    opacity: 1;
    color: #53545b;
}

.to-be-fixed.fixed {
    position: fixed;
    top: 0;
    width: 100%;
    box-shadow: 0 0 30px rgba(0, 0, 0, 0.05);
    z-index: 99;
    transition: 0.4s ease;
    animation: fadeIn 1s ease;
    background-color: var(--white) !important;
}

@keyframes fadeIn {
    0% {
        top: -100%;
        opacity: 0;
    }
    100% {
        top: 0;
        opacity: 1;
    }
}

.bg-defaults {
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
}

.outer-container {
    margin: 0 50px;
}

@media screen and (max-width: 1399px) {
    .outer-container {
        margin: 0;
    }
}

.owl-carousel .owl-item>img {
    display: block;
    width: 100%;
}

.owl-carousel .owl-item img {
    display: inline;
    width: auto;
}

@media screen and (max-width: 479px) {
    .col-xxs-12 {
        width: 100%;
    }
}

.logo img {
    width: 120px;
}

@media screen and (max-width: 767px) {
    .logo img {
        width: 130px;
    }
}

