    text-align: center;
}

.loading-stroke {
    position: relative;
    width: 100%;
    background: #92231d;
    height: 3px;
}

.loading-stroke::before {
    position: absolute;
    content: "";
    left: 0;
    top: 0;
    width: 100%;
    height: 3px;
    background-color: #fff;
    animation: kidba-loading 5s linear infinite;
    animation-timing-function: cubic-bezier(0.75, 0.04, 0, 0.86);
}

.loading-logo-icon {
    position: absolute;
    top: -60px;
    left: 0;
    width: 60px;
    height: 60px;
    animation: kidba-loading 5s linear infinite;
    animation-timing-function: cubic-bezier(0.75, 0.04, 0, 0.86);
}

.loading-logo-icon-2 {
    width: 60px;
    height: 60px;
    transform-origin: center;
    animation: kidba-loading-shake 5s linear infinite;
}

.loading-logo-text {
    padding-bottom: 10px;
}

@keyframes kidba-loading-shake {
    0% {
        transform: rotate(0deg);
    }
    25% {
        transform: rotate(-75deg);
    }
    50% {
        transform: rotate(0deg);
    }
    75% {
        transform: rotate(-75deg);
    }
    100% {
        transform: rotate(0deg);
    }
}

@keyframes kidba-loading {
    0% {
        left: 0;
    }
    50% {
        left: 100%;
    }
    100% {
        left: 0;
    }
}

